<template>
    <div class="footer_Btn">
        <mt-tabbar v-model="selected">
            <mt-tab-item id="首页">
                <router-link to='/' exact>
                    首页
                </router-link>
            </mt-tab-item>
        
            <mt-tab-item id="项目">
                <router-link to='/detail'>
                新建项目
                </router-link>
            </mt-tab-item>
        
        
            <mt-tab-item id="我的">
                <router-link to='/city'>
                我的
                </router-link>
            </mt-tab-item>
        </mt-tabbar>
    </div>
</template>

<script>
export default {
    name:'index',
    data(){
        return{
            selected:''
        }
    }
}
</script>

<style>
.mint-tabbar{
    position: fixed;
    left: 0;
    bottom:0px;
    height: 1.2rem;
    background: #eee;
    border-top: 0.01rem solid #ccc;
}
.footer_Btn a.router-link-active,.router-link-exact-active{
    color: #00bcd4;
}
.footer_Btn a{
    color: #333;
    display: block;
    line-height: 1.2rem;
    text-align: center;
}
.mint-tabbar > .mint-tab-item.is-selected{
    background: initial;
}
</style>



