<template>
  <div class="list" ref="wrapper">
    <div style="column-count: 3;column-gap:0;">
      <div class="list_co">
        <div class="list_co1">
          <h2>市场调查</h2>
          <p>
            通过市场调查分析市场情况，了解市场现状和发展趋势，以避免企业在制定营销策略时发生错误，从而在不断变化的市场中谋取企业最大效益
          </p>
          <mt-button type="primary" size="small">查看详情</mt-button>
        </div>
      </div>
      <div class="list_co">
        <div class="list_co1">
        <h2>微信问卷</h2>
        <p>
          微信已是手机必装软件之一，所以不管是使用微信扫码作答还是做微信相关的调查问卷都是当下非常流行的
        </p>
        <mt-button type="primary" size="small">查看详情</mt-button>
        </div>
      </div>
      <div class="list_co">
        <div class="list_co1">
        <h2>大学生就业调查</h2>
        <p>
          如何了解当代大学生的就业选择、心理、形势等问题？在越来越丰富的工作形态下，个性独特的大学生会如何选择自己的工作？
        </p>
        <mt-button type="primary" size="small">查看详情</mt-button>
        </div>
      </div>
      <div class="list_co">
        <div class="list_co1">
        <h2>客户满意度调查</h2>
        <p>
          通过顾客满意度调查，可以挖掘出导致顾客不满意的关键因素，从而有针对性的改善企业服务质量、提升顾客重复购买率、企业竞争能力及盈利
        </p>
        <mt-button type="primary" size="small">查看详情</mt-button>
        </div>
      </div>
      <div class="list_co">
        <div class="list_co1">
        <h2>员工满意度调查</h2>
        <p>
          只有充分的了解了员工对薪酬、工作、制度、个人发展等方面的需求和期望，才能通过改善这些需求来促进员工的工作热情，提高企业绩效
        </p>
        <mt-button type="primary" size="small">查看详情</mt-button>
        </div>
      </div>
      <div class="list_co">
        <div class="list_co1">
        <h2>游戏调查问卷</h2>
        <p>
          不管是端游，手游还是各种趣味游戏小程序，成功的游戏总是能在很短的时间就吸引大量的用户，这个神奇的行业激起了很多人的兴趣
        </p>
        <mt-button type="primary" size="small">查看详情</mt-button>
        </div>
      </div>
      <div class="list_co">
        <div class="list_co1">
        <h2>品牌知名度调查问卷</h2>
        <p>
          很多商家企业都有自己的品牌和logo，一个成功的品牌可成为公司的代名词，想要了解自家品牌的知名度，对其进行调查是最为方便的
        </p>
        <mt-button type="primary" size="small">查看详情</mt-button>
        </div>
      </div>
      <div class="list_co">
        <div class="list_co1">
        <h2>大学生消费情况调查</h2>
        <p>
          在经济相对自由的学生时代，没有方向的随意消费似乎是当前大学生普遍的消费状况。只有充分进行调查了解，才能帮助建立健康的消费价值观
        </p>
        <mt-button type="primary" size="small">查看详情</mt-button>
        </div>
      </div>
      <div class="list_co">
        <div class="list_co1">
        <h2>大学生调查问卷</h2>
        <p>
          为了更好的了解当代大学生的生活、行为及就业情况和发展趋势，小星整合了一系列大学生调查问卷
        </p>
        <mt-button type="primary" size="small">查看详情</mt-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Bscroll from 'better-scroll'
export default {
  name: 'CityList',
  watch: {
    letter () {
      if(this.letter){
        const element=this.$refs[this.letter][0]
        this.scroll.scrollToElement(element)
      }
    }
  },
  mounted () {
    this.scroll = new Bscroll(this.$refs.wrapper)
  }
}
</script>

<style lang="stylus" scoped>
  .list
    padding: 0.3rem 0 0
    overflow:hidden
    position: absolute
    top:2.86rem
    left:0
    right:0
    bottom:1.2rem
    background: rgb(238, 238, 238)
    .list_co
      box-sizing: border-box
      break-inside: avoid
      padding:0.1rem 0
      margin 0 0.1rem
      margin-bottom:0.3rem
      h2
        font-size: 0.34rem
        color: #08304c
        margin: 0.15rem 0
      p
        font-size: 0.3rem
        color: #808080
        text-indent:0.1rem
      button
        margin: 0.1rem auto
        display: block
    .list_co1
      border-radius:0.1rem
      padding: 0.1rem;
      background-color: #fff;
      -webkit-box-shadow: 0 0 15px rgba(152,152,152,0.5)
      box-shadow: 0 0 15px rgba(152,152,152,0.5)
</style>



